table,
th,
td {
    border: none;
}

table {
    width: 100%;
    display: table;
    border-collapse: collapse;
    border-spacing: 0;
    &.striped {
        tr {
            border-bottom: none;
        }

        > tbody {
            > tr:nth-child(odd) {
                background-color: var(--table-striped-color);
            }

            > tr > td {
                border-radius: 0;
            }
        }
    }

    &.highlight > tbody > tr {
        transition: background-color 0.25s ease;
        &:hover {
            background-color: var(--table-striped-color);
        }
    }

    &.centered {
        thead tr th,
        tbody tr td {
            text-align: center;
        }
    }
}

tr {
    border-bottom: 1px solid var(--table-border-color);
}

td,
th {
    padding: 15px 5px;
    display: table-cell;
    text-align: left;
    vertical-align: middle;
    border-radius: 2px;
}

/* Responsive Table */

@media (--medium-and-down) {
    table.responsive {
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
        display: block;
        position: relative;
        td:empty:before {
            content: "\00a0";
        }

        th,
        td {
            margin: 0;
            vertical-align: top;
        }

        th {
            text-align: left;
            display: block;
            text-align: right;
        }
        thead {
            display: block;
            float: left;
            border: 0;
            border-right: 1px solid var(--table-border-color);
            tr {
                display: block;
                padding: 0 10px 0 0;
                th::before {
                    content: "\00a0";
                }
            }
        }
        tbody {
            display: block;
            width: auto;
            position: relative;
            overflow-x: auto;
            white-space: nowrap;
            tr {
                display: inline-block;
                vertical-align: top;
            }
        }
        td {
            display: block;
            min-height: 1.25em;
            text-align: left;
        }
        tr {
            border-bottom: none;
            padding: 0 10px;
        }
    }
}